@import url('https://fonts.googleapis.com/css2?family=Alegreya:ital,wght@1,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap');

body {
    margin: 0px;
    padding: 0px;

}

* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

.rem-name {
    color: #dc143c;
}

.navbar {
    width: 100%;
    /* position: fixed; */
    top: 0;
    z-index: 99;
    background-color: white;
    /* border: 1px solid red; */
    padding: 10px;




    display: flex;

    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid gainsboro;
    /* background-color: rgb(220, 228, 242); */

}

.logo {
    font-size: 35px;
    font-family: 'Syne Mono', monospace;
    font-weight: bold;


}

.allroutes {
    list-style: none;
    display: flex;
    margin-right: 40px;

}

li {
    font-size: 30px;
    margin-left: 15px;
    cursor: pointer;
    font-weight: bold;
    font-family: 'Alegreya', serif;

}

.ham-burger {
    display: none;
}

@media screen and (max-width: 700px) {

    .ham-burger {
        display: flex
    }

    .allroutes {
        display: none
    }



}