* {
    padding: 0;
    margin: 0
}

#skills {
    width: 100%
}

#wrapper {
    display: flex;
    width: 85%;
    margin: auto;
    align-items: center;
    justify-content: space-between;
}

.image {
    width: 40%;
    /* background-color: green; */

}

.image img {
    width: 100%
}

#tech-container {

    width: 50%;
    /* background-color: pink; */
}

.techstack {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(4, 1fr);
    gap: 50px;
}

.techstack img {
    width: 100%
}

@media (max-width:700px) {
    .techstack {
        grid-template-columns: repeat(3, 1fr);



    }

    #wrapper {
        flex-direction: column-reverse;
        width: 100%
    }

    .image {
        width: 80%;
        /* background-color: green; */

    }

    #tech-container {
        width: 80%
    }
}