* {
    margin: 0;
    padding: 0
}

.project {

    padding: 20px;
    background-color: rgb(220, 228, 242);
    border-radius: 8px;
}

.project * {
    margin-bottom: 10px;
}

.projects {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 75%;
    justify-content: space-around;
    gap: 40px;
    margin: auto;
}

.project>img {
    width: 100%
}

.tech img {
    margin-right: 9px;
}

.project-links {
    display: flex;


}

.project-links * {
    margin-right: 10px;
}

.project-links button {
    padding: 7px 25px 7px 25px;
    font-size: 16px;
}

@media (max-width:700px) {
    .projects {
        grid-template-columns: repeat(1, 1fr);
        width: 75%;

    }
}