* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.profile {
    height: 100vh;
    display: flex;
    width: 80%;
    margin: auto;
    justify-content: space-between;
    align-items: center;
}

.back-img {
    background-image: url('https://i.pinimg.com/originals/99/65/33/996533dbc2e2768a6ad2cebf5ea11210.gif');


}

.profile p {
    font-size: 30px;
}

.hello {
    font-size: 60px;
}

.details {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* background-color: red; */
    height: 60%;
    width: 45%
}

.boyimg {
    width: 50%;



}

.boyimg>img {
    width: 100%
}

.sm * {
    margin-right: 10px;

}

.profile button {
    width: 70%;
    padding: 10px;
    font-size: 20px;
    background-color: #dc143c;
    color: white
}

@media (max-width:700px) {
    .profile {
        flex-direction: column;
        width: 90%;
        justify-content: center;

    }

    .details {
        width: 80%;
        margin-top: 50px;

    }

    .details>* {
        margin-bottom: 20px;

    }

    .boyimg {
        width: 100%
    }

    .hello {
        font-size: 50px;
    }

    .profile p {
        font-size: 20px;
    }

}