.contect-links {
    width: 50%;
    border-bottom: 1px solid gainsboro;
    padding: 30px;
    margin: auto;
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}

.contect {


    margin-top: 100px;
}

.contect-links>div * {
    margin-right: 20px;

}

.message {
    width: 60%;
    margin: auto;
    text-align: center;
    font-size: 16px;
    padding: 20px
}

.reach {
    font-size: 50px;
}

@media (max-width:700px) {
    .contect-links {
        width: 70%
    }

    .message {
        width: 90%;
        font-size: 13px;
    }

    .reach {
        font-size: 30px;
    }


}